* {
    padding: 0;
    margin: 0;
}

.app {
    min-height: 100vh;
    display: flex;
    align-items: center;
    justify-content: center;
}

.cells {
    width: 300px;
    height: 300px;
    display: flex;
    flex-wrap: wrap;
    border: 1px solid black;
}

.cell {
    display: flex;
    align-items: center;
    justify-content: center;
    width: 100px;
    height: 100px;
    border: 1px solid black;
    box-sizing: border-box;
}

.circle {
    width: 80%;
    height: 80%;
    border-radius: 50%;
    border: 10px solid blue;
    box-sizing: border-box;
}

.cross {
    position: relative;
    width: 80%;
    height: 80%;
    transform: rotate(45deg);
}

.cross::before, .cross::after {
    content: '';
    position: absolute;
}

.cross::before {
    left: 0;
    top: calc(50% - 5px);
    width: 100%;
    height: 10px;
    background-color: red;
}

.cross::after {
    left: calc(50% - 5px);
    top: 0;
    width: 10px;
    height: 100%;
    background-color: red;
}

.message {
    text-align: center;
    margin-top: 10px;
}

.restart {
    display: block;
    margin: 10px auto 0;
}